import React, { useState, useEffect } from 'react';
import { AppBar, Toolbar, Typography, Container, Button, Drawer, IconButton } from '@mui/material';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { logoutSuccess } from '../../store/authSlice';
import { Outlet } from 'react-router-dom'; // Outlet 
import UserDashBar from '../panel/UserDashBar';
import { loginSuccess } from '../../store/authSlice';
import MenuIcon from '@mui/icons-material/Menu';

function Layout() {
  const { isAuthenticated } = useSelector((state) => state.auth); //   
  const dispatch = useDispatch();
  const [showDashBar, setShowDashBar] = useState(false);

  useEffect(() => {
    const savedAuth = localStorage.getItem('auth');
    if (savedAuth) {
      const authData = JSON.parse(savedAuth);
      if (authData.isAuthenticated) {
        dispatch(loginSuccess(authData.user)); //   
      }
    }
  }, [dispatch]);

  const handleLogout = () => {
    dispatch(logoutSuccess()); //   
    setTimeout(() => {
        window.location.reload();  //  
    }, 0);
  };

  const toggleDashBar = () => {
    setShowDashBar(!showDashBar); //  
  };

  return (
    <div>
      <AppBar position="sticky" sx={{ backgroundColor: '#C8A2C8' }}>
        <Toolbar>
          {/*    */}
          <IconButton color="inherit" onClick={toggleDashBar}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            <Link to="/" style={{ textDecoration: 'none', color: '#fff' }}>
              Capstone
            </Link>
          </Typography>
          <Button
            component={Link}
            to="/compare-page"
            variant="outlined"
            color="inherit"
            style={{ margin: '0 10px' }}
          >
            !
          </Button>
          <Button
            component={Link}
            to="/space-registeration"
            variant="outlined"
            color="inherit"
            style={{ margin: '0 10px' }}
          >
             
          </Button>
          {!isAuthenticated && (
            <div>
              <Button
                component={Link}
                to="/login"
                variant="outlined"
                color="inherit"
                style={{ margin: '0 10px' }}
              >
                
              </Button>
              <Button
                component={Link}
                to="/register"
                variant="outlined"
                color="inherit"
                style={{ margin: '0 10px' }}
              >
                
              </Button>
            </div>
          )}
          {isAuthenticated && (
            <div>
              <Button color="inherit" onClick={handleLogout}>
                
              </Button>
            </div>
          )}
        </Toolbar>
      </AppBar>

      {/* Drawer  UserDashBar    */}
      <Drawer anchor="left" open={showDashBar} onClose={toggleDashBar}>
        <UserDashBar />
      </Drawer>
      
      <Container style={{ marginTop: '20px' }}>
        {/* children  Outlet  */}
        <Outlet />
      </Container>
    </div>
  );
}

export default Layout;
