import React, { useState } from 'react';
import { Box, Typography, Button, TextField } from '@mui/material';
// import axios from '../../axiosConfig';  //   axios 

const CommentsSection = ({ isLoggedIn, userEmail, locationId }) => {
    const [comments, setComments] = useState([]); //   
    const [newComment, setNewComment] = useState(''); //    

    const handleCommentChange = (e) => {
        setNewComment(e.target.value);
    };

    const handleCommentSubmit = () => {
        /*
        if (!isLoggedIn) {
            alert('     .');
            return;
        }
        */

        const newCommentData = {
            id: comments.length + 1, //  ID,     
            text: newComment,
            userId: userEmail,
        };

        setComments([...comments, newCommentData]);
        setNewComment(''); //   

        //  axios    
        /*
        axios.post('/api/comments', { locationId, comment: newCommentData })
            .then(response => console.log('  ', response))
            .catch(error => console.error('  ', error));
        */
    };

    return (
        <Box>
            <Box sx={{ marginTop: '20px' }}>
                <Typography variant="h6" sx={{ marginBottom: '10px' }}> </Typography>
                {comments.length > 0 ? (
                    comments.map((comment) => (
                        <Box key={comment.id} sx={{ padding: '10px 0', borderBottom: '1px solid #ddd' }}>
                            <Typography variant="body2">{comment.text}</Typography>
                            <Typography variant="caption" color="textSecondary">: {comment.userId}</Typography>
                        </Box>
                    ))
                ) : (
                    <Typography variant="body2"> .</Typography>
                )}
            </Box>
            
            {/*     
            {isLoggedIn ? (
                <>
                    <TextField
                        label=" "
                        variant="outlined"
                        fullWidth
                        multiline
                        value={newComment}
                        onChange={handleCommentChange}
                    />
                    <Button 
                        variant="contained" 
                        sx={{ marginTop: '10px' }} 
                        onClick={handleCommentSubmit} 
                        disabled={!newComment.trim()}
                    >
                         
                    </Button>
                </>
            ) : (
                <Typography variant="body1">     .</Typography>
            )}
            */}

            {/*     */}  
            <TextField
                label=" "
                variant="outlined"
                fullWidth
                multiline
                value={newComment}
                onChange={handleCommentChange}
            />
            <Button 
                variant="contained" 
                sx={{ marginTop: '10px' }} 
                onClick={handleCommentSubmit} 
                disabled={!newComment.trim()}
            >
                 
            </Button>
        </Box>
    );
};

export default CommentsSection;
