import React, { useState, useEffect } from 'react';
import { Box, Select, MenuItem, Button } from '@mui/material'; // Button 
import { useNavigate } from 'react-router-dom';
import coordinates from '../map/KakaoMap'
import axios from "axios"; // React Router useNavigate 
// import axios from '../../axiosConfig';  //     axios 

const InfoPanel = ({ locations }) => {
  const [locationData, setLocationData] = useState([]);
  const [sortedData, setSortedData] = useState([]);
  const [sortKey, setSortKey] = useState('default');
  const navigate = useNavigate(); //     

  //    
  const setTemporaryData = () => {
    const tempData = Array.from({ length: 10 }, (_, index) => ({
      id: index,
      spaceName: `  ${index + 1}`,
      mainImageBase64: 'https://via.placeholder.com/50',
      spaceIntro: `   ${index + 1}  .`,
      spaceFee: Math.floor(Math.random() * 100000), //  
      reviewCount: Math.floor(Math.random() * 100),  //   
    }));
    setLocationData(tempData);
    setSortedData(tempData); //    
  };

  useEffect(() => {
    setSortedData(locations);

    const fetchData = async () => {
       try {
           const params = {
               latitude : coordinates.lat || 0,
               longitude: coordinates.lng || 0,
               distance : 10.0
           };

         const response = await axios.get('/venues/locationSearch', {params});
         const data = response.data;

           const imagePromises = data.map((venue) =>
               axios.get(`/venuePhoto/${venue.venue_id}`)
           );

           const images = await Promise.all(imagePromises);
           const imageData = images.map((image) =>
               image.data[0]?.photoBase64 || 'https://via.placeholder.com/150'
           );

         //    ,    
         if (Array.isArray(data)) {
             const list = data.map((venue, index) => {
                 return {
                     id: venue.venue_id || index,
                     spaceName: venue.name || `  ${index + 1}`,
                     mainImageBase64: imageData[index],
                     spaceIntro: venue.simpledescription || `   ${index + 1}  .`,
                     spaceFee: venue.rental_fee || Math.floor(Math.random() * 100000), //  
                     reviewCount: Math.floor(Math.random() * 100),  //   
                 }
             });
           setLocationData(list);
             setSortedData(list);
         } else {
           setTemporaryData();
         }
       } catch (error) {
         // axios      
           console.log(error)
         setTemporaryData();
       }
    };

    fetchData();

    //   
    //setTemporaryData();
  }, [locations]);

  //  
  const sortData = (data, key) => {
    let sortedArray = [...data];
    switch (key) {
      case 'spaceFee-high':
        sortedArray.sort((a, b) => b.spaceFee - a.spaceFee); //   
        break;
      case 'spaceFee-low':
        sortedArray.sort((a, b) => a.spaceFee - b.spaceFee); //   
        break;
      case 'reviewCount':
        sortedArray.sort((a, b) => b.reviewCount - a.reviewCount); //   
        break;
      default:
        break;
    }
    return sortedArray;
  };

  //     
  const handleSortChange = (event) => {
    const newSortKey = event.target.value;
    setSortKey(newSortKey);
    const sorted = sortData(locationData, newSortKey);
    setSortedData(sorted);
  };

  //       
  const handleLocationClick = (location) => {
    if (!location || typeof location.id === 'undefined') {
      console.error('Invalid location object:', location);
      return;
    }
    navigate(`/rental-space/${location.id}`); // ID   
  };

  return (
    <Box
      sx={{
        width: '550px',
        height: '480px', //    
        backgroundColor: '#f9f9f9',
        padding: '10px',
        marginLeft: '20px',
        borderRadius: '5px',
        boxShadow: '0 2px 8px rgba(0, 0, 0, 0.1)',
        position: 'relative', //    
        display: 'flex',
        flexDirection: 'column', //     
      }}
    >
      {/*   -     */}
      <Box
        sx={{
          position: 'sticky', //   
          top: 0,
          zIndex: 1,
          backgroundColor: '#f9f9f9',
          paddingBottom: '10px',
          textAlign: 'right', //   
          display: 'flex', 
          justifyContent: 'flex-end', //    
        }}
      >
        <Select
          value={sortKey}
          onChange={handleSortChange}
          sx={{
            width: '165px',
            height: '40px',
            marginBottom: '10px',
            fontSize: '14px',
            textAlign: 'center',
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <MenuItem value="default">  </MenuItem>
          <MenuItem value="spaceFee-high">  </MenuItem>
          <MenuItem value="spaceFee-low">  </MenuItem>
          <MenuItem value="reviewCount">  </MenuItem>
        </Select>
      </Box>

      {/*   -    */}
      <Box
        sx={{
          overflowY: 'scroll',
          flexGrow: 1, //     
        }}
      >
        {sortedData.map((location, index) => (
          <Button
            key={index}
            variant="outlined"
            fullWidth
            onClick={() => handleLocationClick(location)} //      
            sx={{
              marginBottom: '10px',
              height: '80px', //    2 
              display: 'flex',
              justifyContent: 'flex-start', //  
              alignItems: 'center', //   
              padding: '10px', //   
            }}
          >
            {/*   */}
            <img
              src={location.mainImageBase64}
              alt={location.spaceName}
              style={{ width: '50px', height: '50px', marginRight: '15px' }}
            />
            {/*   */}
            <Box sx={{ textAlign: 'left' }}>
              <div style={{ fontWeight: 'bold' }}>{location.spaceName}</div>
              <div>{location.spaceIntro}</div>
            </Box>
          </Button>
        ))}
      </Box>
    </Box>
  );
};

export default InfoPanel;
