// Prompt.jsx
import React, { useState, useRef, useEffect, Suspense } from 'react';
import { Box, Button, TextField, Typography, CircularProgress } from '@mui/material';
import { useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import axios from 'axios';
import { useQuery, QueryClient, QueryClientProvider } from 'react-query';

// React Query  
const queryClient = new QueryClient();

//   
const fetchVenueInfo = async (venueId, prompt, fileData) => {
  const response = await axios.post(`/search/getResponse/${venueId}`, {
    Tokens: [
      {
        Require: prompt,
        Subject: '',
        Summary: '',
        Token: fileData?.fileId || '',
      },
    ],
  });
  return response.data;
};

const fetchVenuePhoto = async (venueId) => {
  const response = await axios.get(`/venuePhoto/${venueId}`);
  const imageBase64 = response.data[0]?.photoBase64 || '';
  const imageData = /^data:image\/[a-zA-Z]+;base64,/.test(imageBase64)
    ? imageBase64
    : `data:image/jpeg;base64,${imageBase64}`;
  return imageData;
};

//  
function VenuePhoto({ venueId }) {
  const { data: photoData } = useQuery(
    ['venuePhoto', venueId],
    () => fetchVenuePhoto(venueId),
    { suspense: true }
  );

  return (
    <img
      src={photoData}
      alt="Venue"
      style={{ width: '150px', height: '150px', marginRight: '20px' }}
    />
  );
}

//   
function VenueInfo({ venueId, prompt, fileData }) {
  const { data: venueData } = useQuery(
    ['venueInfo', venueId],
    () => fetchVenueInfo(venueId, prompt, fileData),
    { suspense: true }
  );

  return (
    <Box>
      <Typography>
        <strong>:</strong> {venueData.name}
      </Typography>
      <Typography>
        <strong>:</strong> {venueData.location}
      </Typography>
      <Typography>
        <strong>:</strong> {venueData.amount}
      </Typography>
      <Typography>
        <strong> :</strong> {venueData.simpleDesc}
      </Typography>
      {/*    */}
      {(venueData.caution || venueData.recommand) && (
        <Box
          sx={{
            border: '1px solid #ccc',
            borderRadius: 2,
            p: 2,
            mt: 2,
            backgroundColor: '#f9f9f9',
          }}
        >
          {venueData.caution && (
            <Typography sx={{ mb: 1 }}>
              <strong>:</strong> {venueData.caution.join(' ')}
            </Typography>
          )}
          {venueData.recommand && (
            <Typography>
              <strong> :</strong> {venueData.recommand.join(' ')}
            </Typography>
          )}
        </Box>
      )}
    </Box>
  );
}

//   
function VenueCard({ venueId, prompt, fileData }) {
  return (
    <Box
      sx={{
        display: 'flex',
        mb: 2,
        border: '1px solid #eee',
        borderRadius: 2,
        p: 2,
        cursor: 'pointer',
      }}
      onClick={() => window.open(`/rental-space/${venueId}`, '_blank')}
    >
      <Suspense
        fallback={
          <Box
            sx={{
              width: '150px',
              height: '150px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              backgroundColor: '#f0f0f0',
              marginRight: '20px',
            }}
          >
            <CircularProgress size={24} />
          </Box>
        }
      >
        <VenuePhoto venueId={venueId} />
      </Suspense>
      <Suspense fallback={<CircularProgress />}>
        <VenueInfo venueId={venueId} prompt={prompt} fileData={fileData} />
      </Suspense>
    </Box>
  );
}

//   
function InitialPrompt({ handleSubmit, handleFileChange, file, prompt, setPrompt, isLoading }) {
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        flexGrow: 1,
      }}
    >
      <Typography variant="h4" gutterBottom>
         ?
      </Typography>
      <Typography variant="h6" gutterBottom>
        -      .
      </Typography>
      <Typography variant="h6" gutterBottom>
        -      .
      </Typography>
      {/*   */}
      <Box sx={{ width: '100%', maxWidth: 800, mt: 2 }}>
        <form onSubmit={handleSubmit} style={{ width: '100%' }}>
          <Box sx={{ position: 'relative', mt: 2 }}>
            <TextField
              label="   !"
              multiline
              margin="normal"
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              variant="outlined"
              maxRows={10}
              sx={{
                width: '500px',
                pr: '70px', //   
              }}
            />
            <Button
              type="submit"
              variant="contained"
              color="primary"
              sx={{
                position: 'absolute',
                right: 8,
                bottom: 8,
                minWidth: '48px',
                minHeight: '57px',
                padding: '8px',
              }}
              disabled={isLoading}
            >
              
            </Button>
          </Box>
        </form>
      </Box>
    </Box>
  );
}

//   
function ResponseDisplay({
  response,
  isLoading,
  error,
  handleSubmit,
  handleFileChange,
  file,
  prompt,
  setPrompt,
  fileData,
}) {
  const bottomRef = useRef(null);

  useEffect(() => {
    if (response.length > 0) {
      bottomRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [response]);

  return (
    <>
      {/* AI    */}
      <Box sx={{ width: '90%', maxWidth: 800, flexGrow: 1, mb: 3, mt: 2 }}>
        {error && (
          <Box sx={{ color: 'red', mt: 2 }}>
            <Typography> .   .</Typography>
          </Box>
        )}

        {response.map((res, index) => (
          <Box key={index} sx={{ width: '100%', mt: 4 }}>
            {/*  */}
            {res.feedback && res.feedback.length > 0 && (
              <Box sx={{ p: 2, mb: 3, border: '1px solid #ccc', borderRadius: 2 }}>
                <Typography variant="h6"></Typography>
                {res.feedback.map((item, idx) => (
                  <Typography key={idx}>{item}</Typography> //    
                ))}
              </Box>
            )}

            {/*    */}
            <Box sx={{ p: 2, border: '1px solid #ccc', borderRadius: 2 }}>
              <Typography variant="h6">  </Typography>
              {res.venueIds.slice(0, 5).map((venueId, index) => (
                <VenueCard key={index} venueId={venueId} prompt={prompt} fileData={fileData} />
              ))}
            </Box>

            {/*     */}
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  const leftFacilityId = res.venueIds[0]?.toString() || 'temp1';
                  const rightFacilityId = res.venueIds[1]?.toString() || 'temp2';

                  //  
                  sessionStorage.setItem(
                    'compareFacilities',
                    JSON.stringify({
                      facilities: res.venueIds,
                      leftFacilityId,
                      rightFacilityId,
                    })
                  );

                  //   
                  window.open('/compare-page-prompt', '_blank'); //   
                }}
              >
                  
              </Button>
            </Box>
          </Box>
        ))}
      </Box>

      {/*    */}
      <Box
        sx={{
          position: 'sticky',
          bottom: 0,
          width: '100%',
          maxWidth: 800,
          backgroundColor: 'white',
          zIndex: 1,
          boxShadow: '0px -2px 10px rgba(0, 0, 0, 0.1)',
          padding: 2,
        }}
      >
        {isLoading && (
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'center',
              position: 'absolute', //    
              top: '-50px', //    
              left: '50%',
              transform: 'translateX(-50%)',
              zIndex: 2,
            }}
          >
            <CircularProgress />
          </Box>
        )}

        <form onSubmit={handleSubmit} style={{ width: '100%' }}>
          <Box sx={{ position: 'relative', mt: 2 }}>
            <TextField
              label="     !"
              multiline
              margin="normal"
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              variant="outlined"
              maxRows={6}
              sx={{
                width: 'calc(100% - 70px)',
                pr: '0px', //   
              }}
            />

            <Button
              type="submit"
              variant="contained"
              color="primary"
              sx={{
                position: 'absolute',
                right: 8,
                bottom: 8, //     
                minWidth: '48px',
                minHeight: '57px',
                padding: '8px',
              }}
              disabled={isLoading}
            >
              
            </Button>
          </Box>
        </form>
      </Box>
      <div ref={bottomRef} />
    </>
  );
}

//  Prompt 
function Prompt() {
  const location = useLocation();
  const auth = useSelector((state) => state.auth);

  const [prompt, setPrompt] = useState(location.state?.prompt || '');
  const [response, setResponse] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [file, setFile] = useState(location.state?.file || null);
  const [fileData, setFileData] = useState(null);

  const handleFileUpload = async (file) => {
    const formData = new FormData();
    formData.append('file', file);

    try {
      const res = await axios.post('/fileAssist', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return res.data; //     
    } catch (error) {
      console.error('  :', error);
      throw new Error('  .');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setIsLoading(true);

    const uid = auth?.user?.id || 0;

    if (file) {
      try {
        const uploadedFileData = await handleFileUpload(file);
        setFileData(uploadedFileData);
      } catch (error) {
        setError('    .');
        setIsLoading(false);
        return;
      }
    }

    try {
      // 1.   
      const searchRes = await axios.post('/search/searchKeyword', {
        keyword: prompt,
        uid,
      });

      let venueIds = searchRes.data.searchResults.map((result) => result.venueId);
      const feedback = searchRes.data.feedback;

      if (venueIds.length === 0) {
        throw new Error('  .');
      }

      venueIds = venueIds.slice(0, 5);

      //   
      setResponse((prevResponse) => [
        ...prevResponse,
        { feedback, venueIds },
      ]);
    } catch (error) {
      setError('    .');
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  return (
    <QueryClientProvider client={queryClient}>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', minHeight: '100vh' }}>
        {response.length === 0 && !isLoading && !error ? (
          <InitialPrompt
            handleSubmit={handleSubmit}
            handleFileChange={handleFileChange}
            file={file}
            prompt={prompt}
            setPrompt={setPrompt}
            isLoading={isLoading}
          />
        ) : (
          <ResponseDisplay
            response={response}
            isLoading={isLoading}
            error={error}
            handleSubmit={handleSubmit}
            handleFileChange={handleFileChange}
            file={file}
            prompt={prompt}
            setPrompt={setPrompt}
            fileData={fileData}
          />
        )}
      </Box>
    </QueryClientProvider>
  );
}

export default Prompt;
