//      

import React, { useState } from 'react';
import { TextField, Button, Box, Typography, CircularProgress } from '@mui/material';
import axios from 'axios';

function CommonPrompt() {
  const [commonPrompt, setCommonPrompt] = useState(''); //    
  const [response, setResponse] = useState(''); // GPT  
  const [error, setError] = useState(null); //   
  const [isLoading, setIsLoading] = useState(false); //   

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null); //  
    setIsLoading(true); //  

    try {
      // axios   API 
      const res = await axios.post('/gptCall/Call', {
        question: commonPrompt, // Question   key "question" 
      });

      setResponse(`AI : ${res.data}`); //    
    } catch (error) {
      //      
      console.error("Error during API call:", error);
      setError(true);
      setResponse(`AI (): ${commonPrompt}`);
    } finally {
      setIsLoading(false); //  
    }
  };

  return (
    <Box sx={{ maxWidth: 600, mx: 'auto', mt: 5, p: 3, boxShadow: 3, borderRadius: 2 }}>
      <Typography variant="h5" component="h1" gutterBottom align="center">
        AI    !
      </Typography>
      <form onSubmit={handleSubmit}>
        <TextField
          label="  "
          multiline
          rows={4}
          fullWidth
          margin="normal"
          value={commonPrompt}
          onChange={(e) => setCommonPrompt(e.target.value)}
          variant="outlined"
        />
        <Button type="submit" variant="contained" color="primary" fullWidth sx={{ mt: 2 }} disabled={isLoading}>
          
        </Button>
      </form>

      {/*   */}
      {isLoading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
          <CircularProgress />
        </Box>
      )}

      {/* GPT    */}
      {response && (
        <Box sx={{ mt: 4, p: 2, border: '1px solid #ccc', borderRadius: 2 }}>
          <Typography variant="h6"></Typography>
          <Typography>{response}</Typography>
        </Box>
      )}

      {/*    */}
      {error && (
        <Box sx={{ mt: 2, p: 2, border: '1px solid red', borderRadius: 2 }}>
          <Typography color="error">   .    .</Typography>
        </Box>
      )}
    </Box>
  );
}

export default CommonPrompt;
