import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Avatar, Box, Button, Typography, List, ListItem, ListItemText, Paper, Drawer } from '@mui/material';
import LogoutIcon from '@mui/icons-material/Logout';
import AccessibilityNewIcon from '@mui/icons-material/AccessibilityNew';
import axios from '../../axiosConfig'; // axios 
import { logoutSuccess } from '../../store/authSlice';

//   
const dummyUser = {
  name: "",
  email: "hong@example.com",
  profileImage: "https://via.placeholder.com/150",
  reservations: [
    { id: 1, spaceName: " A", date: "2024-10-05", status: "" },
    { id: 2, spaceName: " B", date: "2024-10-12", status: "" },
  ],
};

const UserDashBar = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(true);
  /*
  const [user, setUser] = useState(dummyUser);
  const [reservations, setReservations] = useState(user.reservations);
  */
  const authState = useSelector((state) => state.auth);

  const user = authState.isAuthenticated ? authState.user || dummyUser : dummyUser;
  const reservations = user.reservations || dummyUser.reservations;

  useEffect(() => {
    //    
    /*
    const fetchData = async () => {
      try {
        const response = await axios.get('/api/user/dashBar'); //   
        const fetchedData = response.data;

        //     
        setIsAuthenticated(true);
        setUser(fetchedData);
        setReservations(fetchedData.reservations);
      } catch (error) {
        console.error('    :', error);
        setIsAuthenticated(true);
      }
    };

    fetchData();
    */
  }, []);

  const handleLogin = (user) => {
    setSidebarOpen(false);
    navigate(`/login`);
    setTimeout(() => {
        window.location.reload();  //  
    }, 0);
  };

  const handleUser = (user) => {
    setSidebarOpen(false);
    navigate(`/user-page?user=${user.name}`);
    setTimeout(() => {
        window.location.reload();  //  
    }, 0);
  };

  //   
  const handleLogout = async () => {
    console.log(" ");
    try {
      //    (  )
      await axios.post('/api/auth/logout');

      //    Redux  
      dispatch(logoutSuccess());
      setIsAuthenticated(false);
      setTimeout(() => {
        window.location.reload();  //  
      }, 0);
    } catch (error) {
      setTimeout(() => {
        window.location.reload();  //  
      }, 0);
      console.error('   :', error);
    }
  };

  return (
    <Drawer
      variant="persistent"
      open={sidebarOpen} //  /   Drawer  
      onClose={() => setSidebarOpen(false)} //  
    >
        <Box sx={{ padding: '20px', maxWidth: '900px', margin: 'auto', marginTop: '10px' }}>
            {/* 1.    */}
            {authState.isAuthenticated ? (
              <Paper elevation={3} sx={{ padding: '20px', marginBottom: '20px' }}>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <Avatar src={user.profileImage} alt={user.name} sx={{ width: 100, height: 100, marginRight: '20px' }} />
                  <Box>
                    <Typography variant="h5">{user.name}</Typography>
                    <Typography variant="body1">{user.email}</Typography>
                    <Button variant="outlined" sx={{ marginTop: '10px' }}> </Button>
                  </Box>
                </Box>
              </Paper>
            ) : (
              <Paper elevation={3} sx={{ padding: '20px', marginBottom: '20px' }}>
                <Typography variant="h6">, !</Typography>
                <Typography>    .</Typography>
              </Paper>
            )}

            {/* 2.     */}
            {isAuthenticated && (
              <Paper elevation={3} sx={{ padding: '20px', marginBottom: '20px' }}>
                  <Typography variant="h6" gutterBottom> </Typography>
                  <List>
                  {reservations.length === 0 ? (
                      <Typography>  .</Typography>
                  ) : (
                      reservations.map((reservation) => (
                      <ListItem key={reservation.id}>
                          <ListItemText
                          primary={reservation.spaceName}
                          secondary={`: ${reservation.date} | : ${reservation.status}`}
                          />
                      </ListItem>
                      ))
                  )}
                  </List>
                  <Button variant="contained" sx={{ marginTop: '10px' }}>  </Button>
              </Paper>
            )}

            {/* 3.     */}
            {/* /   */}
            <Paper elevation={3} sx={{ padding: '20px', marginBottom: '20px' }}>
            {authState.isAuthenticated ? (
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Button
                  variant="contained"
                  color="secondary"
                  startIcon={<AccessibilityNewIcon />}
                  onClick={handleUser}
                  sx={{ flexGrow: 1, marginRight: 'auto', minWidth: '110px' }}
                >
                  
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  startIcon={<LogoutIcon />}
                  onClick={handleLogout}
                  sx={{ flexGrow: 1, marginLeft: 'auto', minWidth: '110px' }}
                >
                  
                </Button>
              </Box>
            ) : (
              <Box sx={{ display: 'flex', justifyContent: 'center' }}>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleLogin}
                  sx={{ flexGrow: 0, minWidth: '150px' }}
                >
                  
                </Button>
              </Box>
            )}
            </Paper>
        </Box>
    </Drawer>
  );
};

export default UserDashBar;
