import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Box, Button, TextField, Typography, MenuItem, Select, FormControl, InputLabel } from '@mui/material';
import axios from '../../axiosConfig';
import Compressor from 'compressorjs';
import EquipmentChecklist from '../panel/EquipmentCheckList';

// Daum Postcode API    
const loadDaumPostcodeScript = () => {
  const script = document.createElement('script');
  script.src = '//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
  script.async = true;
  document.body.appendChild(script);
};

const SpaceRegistration = () => {
  //  
  const ownerId = useSelector((state) => state.auth.user?.id);
  const [spaceName, setSpaceName] = useState('');
  const [spaceType, setSpaceType] = useState('');
  const [spaceIntro, setSpaceIntro] = useState('');
  const [spaceDescription, setSpaceDescription] = useState('');
  const [spaceFee, setSpaceFee] = useState('');
  const [spaceArea, setSpaceArea] = useState('');
  const [spaceCapacity, setSpaceCapacity] = useState('');
  const [spaceTags, setSpaceTags] = useState([]);
  const [facilityInfo, setFacilityInfo] = useState('');
  const [precautions, setPrecautions] = useState('');
  const [refundPolicy, setRefundPolicy] = useState('');
  const [website, setWebsite] = useState('');
  const [mainImageBase64, setMainImageBase64] = useState(null);
  const [postalCode, setPostalCode] = useState('');
  const [roadAddress, setRoadAddress] = useState('');
  const [detailAddress, setDetailAddress] = useState('');
  const [coordinates, setCoordinates] = useState(null);
  const [selectedEquipment, setSelectedEquipment] = useState([]);
  // const [additionalImagesBase64, setAdditionalImagesBase64] = useState([]);

  const [popularTags, setPopularTags] = useState([]);

  //  
  const [inputTag, setInputTag] = useState('');
  const [mainImageName, setMainImageName] = useState('');
  // const [additionalImageNames, setAdditionalImageNames] = useState([]);

  const navigate = useNavigate();

  // Daum Postcode  
  useEffect(() => {
    //    
    const fetchPopularTags = async () => {
      try {
        const response = await axios.get('/search-summary/latest'); // API 
        const tags = response.data
          .slice(0, 5) //  5  
          .map(tag => `#${tag.tokenText.replace('O/', '')}`);
        setPopularTags(tags);
      } catch (error) {
        console.error('Failed to fetch popular tags:', error);
        setPopularTags(['#1', '#2', '#3', '#4', '#5']); //  
      }
    };
    fetchPopularTags();

    loadDaumPostcodeScript();
  }, []);

  // Kakao API   axios 
  const kakaoInstance = axios.create({
    baseURL: '',  // baseURL    Kakao API 
  });

  // Kakao API  
  const getCoordinates = async (address) => {
    try {
      const response = await kakaoInstance.get(
          `/v2/local/search/address.json?query=${encodeURIComponent(address)}`, //  
          {
            headers: {
              Authorization: `KakaoAK ${process.env.REACT_APP_KAKAO_REST_KEY}`,
            },
          }
      );
      if (response.data.documents.length > 0) {
        const { x: longitude, y: latitude } = response.data.documents[0].road_address;
        setCoordinates({ latitude, longitude });
      } else {
        console.error('Coordinates not found');
      }
    } catch (error) {
      console.error('Error fetching coordinates:', error);
    }
  };

  //   
  const handleTagInput = (e) => {
    if (e.key === ' ') {
      e.preventDefault();
      if (inputTag.trim() !== '') {
        setSpaceTags([...spaceTags, `#${inputTag.trim()}`]);
        setInputTag('');
      }
    } else {
      setInputTag(e.target.value);
    }
  };

  const handleTagDelete = (index) => {
    setSpaceTags(spaceTags.filter((_, i) => i !== index));
  };

  //  
  const handlePostcodeSearch = () => {
    new window.daum.Postcode({
      oncomplete: (data) => {
        const roadAddr = data.roadAddress;
        const extraRoadAddr = data.bname ? `(${data.bname})` : '';
        setPostalCode(data.zonecode);
        setRoadAddress(roadAddr + extraRoadAddr);
        getCoordinates(roadAddr);
      },
    }).open();
  };

  // File Base64  
  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  //   
  const handleMainImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      new Compressor(file, {
        quality: 0.6,
        success(result) {
          convertToBase64(result).then((base64) => {
            setMainImageBase64(base64);
            setMainImageName(result.name);
          });
        },
        error(err) {
          console.error(err.message);
        },
      });
    }
  };

  //   
  /*
  const handleImageChange = (event) => {
    const files = event.target.files;
    if (files.length + additionalImagesBase64.length <= 10) {
      const compressedFilesPromises = Array.from(files).map((file) =>
        new Promise((resolve, reject) => {
          new Compressor(file, {
            quality: 0.6,
            success(result) {
              convertToBase64(result).then((base64) => {
                resolve(base64);
              });
            },
            error(err) {
              reject(err);
            },
          });
        })
      );

      Promise.all(compressedFilesPromises)
        .then((base64Images) => {
          setAdditionalImagesBase64([...additionalImagesBase64, ...base64Images]);
          setAdditionalImageNames([...additionalImageNames, ...Array.from(files).map((file) => file.name)]);
        })
        .catch((err) => {
          console.error('Image compression error:', err);
        });
    } else {
      alert('  10   .');
    }
  };
  */

  //      
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const sanitizedTags = spaceTags.map(tag => tag.replace(/^#/, ''));
      console.log('Sanitized tags:', sanitizedTags);
      const data = {
        //  
        "ownerId":                ownerId,                //  id
        "name":                   spaceName,              //   - X string
        "spaceType":              spaceType,              //   - O
        "simpleDescription":      spaceIntro,             //   - X string
        "description":            spaceDescription,       //   - X string
        "rentalFee":              spaceFee,               //   - O
        "area":                   spaceArea,              //   - O
        "capacity":               spaceCapacity,          //   - O int
        "facilityInfo":           facilityInfo,           //   - X string
        "precautions":            precautions,            //    - X string
        "refundPolicy":           refundPolicy,           //   - X string
        "websiteURL":             website,                //  URL - X string
        "postalCode":             postalCode,             //  - X int -  
        "address":                roadAddress,            //   - O
        "detailAddress":          detailAddress,          //   - X string
        "latitude":               coordinates ? coordinates.latitude : null, //  - O
        "longitude":              coordinates ? coordinates.longitude : null, //  - O
        // "additionalImagesBase64": additionalImagesBase64, //   - X string  - 
      };

      console.log('Data: ', data)

      const response = await axios.post('/venues/create', data, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      const venueId = response.data.venueId;

      // spaceTags  API 
      if (sanitizedTags.length > 0) {
        const tagsResponse = await axios.post('/tag/create', {
          "venueId": venueId,
          "tags": sanitizedTags,
        });
        console.log('Tags Response:', tagsResponse.data);
      }

      // mainImageBase64  API 
      if (mainImageBase64) {
        const imageResponse = await axios.post('/venuePhoto/create', {
          "venueId": venueId,
          "base64Image": mainImageBase64,
        });
        console.log('Image Response:', imageResponse.data);
      }

      // selectedEquipment  API 
      if (selectedEquipment.length > 0) {
        const sanitizedEquipment = selectedEquipment.map(Number);
        const equipmentResponse = await axios.post('/equipment/create', {
          "venueId": venueId,
          "equipments": sanitizedEquipment,
        });
        console.log('Equipment Response:', equipmentResponse.data);
      }

      console.log('Response data:', response.data);
      alert('   !');
      navigate('/');
    } catch (error) {
      console.error('Error saving space information:', error);
      alert('  .');
    }
  };

  return (
    <Box sx={{ padding: '20px', maxWidth: '800px', margin: 'auto' }}>
      <Typography variant="h4" gutterBottom align="center">
           
      </Typography>
      <hr style={{ border: 'none', borderTop: '1px solid #ddd', margin: '10px 0' }} />

      <form onSubmit={handleSubmit}>
        {/* 1.   */}
        <Typography variant="h6" gutterBottom>
          
        </Typography>
        <TextField
          label=" ,          ."
          fullWidth
          required
          value={spaceName}
          onChange={(e) => setSpaceName(e.target.value)}
          margin="normal"
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 2.    */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <FormControl fullWidth margin="normal">
          <InputLabel>  .</InputLabel>
          <Select
            label=" "
            required
            value={spaceType}
            onChange={(e) => setSpaceType(e.target.value)}
          >
            <MenuItem value="" disabled>
              
            </MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value="" disabled>
              
            </MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value="" disabled>
              
            </MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value="" disabled>
              
            </MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
          </Select>
        </FormControl>

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 3.     */}
        <Typography variant="h6" gutterBottom>
             
        </Typography>
        <TextField
          label="      ."
          fullWidth
          required
          value={spaceIntro}
          onChange={(e) => setSpaceIntro(e.target.value)}
          margin="normal"
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 4.   */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <TextField
          label=" , ,     ."
          fullWidth
          required
          value={spaceDescription}
          onChange={(e) => setSpaceDescription(e.target.value)}
          margin="normal"
          multiline
          rows={4}
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/*    */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', marginTop: 2 }}>
          <TextField
            label="     "
            fullWidth
            required
            type="number"
            value={spaceFee}
            onChange={(e) => setSpaceFee(e.target.value)}
            margin="normal"
          />
          <Typography variant="body1" sx={{ marginLeft: 1 }}>
            
          </Typography>
        </Box>

        <Box sx={{ marginTop: 2 }}></Box>

        {/*    */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', marginTop: 2 }}>
          <TextField
            label="  "
            fullWidth
            required
            type="number"
            value={spaceArea}
            onChange={(e) => setSpaceArea(e.target.value)}
            margin="normal"
          />
          <Typography variant="body1" sx={{ marginLeft: 1 }}>
            
          </Typography>
        </Box>

        <Box sx={{ marginTop: 2 }}></Box>

        {/*    */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', marginTop: 2 }}>
          <TextField
            label="   ."
            fullWidth
            required
            type="number"
            value={spaceCapacity}
            onChange={(e) => setSpaceCapacity(e.target.value)}
            margin="normal"
          />
          <Typography variant="body1" sx={{ marginLeft: 1 }}>
            
          </Typography>
        </Box>

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 5.   */}
        <Typography variant="h6" gutterBottom sx={{ marginBottom: '0px' }}>
           
        </Typography>
        <Typography variant="body2" sx={{ color: '#757575', mb: 2 }}>
            : {popularTags.join(' ')}
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', gap: 1, mb: 2 }}>
          {spaceTags.map((tag, index) => (
            <Box
              key={index}
              sx={{
                backgroundColor: '#e0e0e0',
                padding: '5px 10px',
                borderRadius: '15px',
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <Typography variant="body2" sx={{ mr: 1 }}>
                {tag}
              </Typography>
              <Button
                size="small"
                onClick={() => handleTagDelete(index)}
                sx={{
                  minWidth: '24px',
                  padding: '2px',
                  fontSize: '12px',
                }}
              >
                X
              </Button>
            </Box>
          ))}
        </Box>
        <TextField
          label=" . ( )"
          fullWidth
          value={inputTag}
          onChange={(e) => setInputTag(e.target.value)}
          onKeyDown={handleTagInput}
          margin="normal"
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 6.  */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <TextField
          label="  ."
          fullWidth
          required
          value={facilityInfo}
          onChange={(e) => setFacilityInfo(e.target.value)}
          margin="normal"
          multiline
          rows={3}
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 7.   */}
        <Typography variant="h6" gutterBottom>
            
        </Typography>
        <TextField
          label="   ."
          fullWidth
          required
          value={precautions}
          onChange={(e) => setPrecautions(e.target.value)}
          margin="normal"
          multiline
          rows={3}
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/*   */}
        <Typography variant="h6" gutterBottom>
           
        </Typography>
        <TextField
          label="  ."
          fullWidth
          required
          value={refundPolicy}
          onChange={(e) => setRefundPolicy(e.target.value)}
          margin="normal"
          multiline
          rows={3}
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 8.  */}
        <Typography variant="h6" gutterBottom>
           URL
        </Typography>
        <TextField
          label=" URL ."
          fullWidth
          value={website}
          onChange={(e) => setWebsite(e.target.value)}
          margin="normal"
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 9.    */}
        <Box
          sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 1 }}
        >
          <Typography variant="h6"> </Typography>
          <Typography variant="caption" color="textSecondary">
             3MB
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'center', marginTop: 1 }}>
          <Box sx={{ flexGrow: 1, marginRight: 1 }}>
            <TextField
              variant="outlined"
              size="small"
              fullWidth
              value={mainImageName || '  .'}
              disabled
            />
          </Box>
          <Button variant="outlined" component="label">
             
            <input type="file" accept="image/*" hidden onChange={handleMainImageChange} />
          </Button>
        </Box>

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 10.   
        <Box
          sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 1 }}
        >
          <Typography variant="h6"> </Typography>
          <Typography variant="caption" color="textSecondary">
             3MB
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'flex-start', marginTop: 1 }}>
          <Box sx={{ flexGrow: 1, marginRight: 1 }}>
            <TextField
              variant="outlined"
              size="small"
              fullWidth
              multiline
              rows={10}
              maxRows={10}
              value={additionalImageNames.join('\n') || '  .'}
              disabled
              sx={{ overflow: 'auto' }}
            />
          </Box>
          <Button variant="outlined" component="label" sx={{ height: '40px' }}>
             
            <input type="file" accept="image/*" multiple hidden onChange={handleImageChange} />
          </Button>
        </Box>
         */}

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 11.   */}
        <Typography variant="h6" gutterBottom>
          
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <TextField
            label="    ."
            fullWidth
            value={roadAddress}
            margin="normal"
            disabled
          />
          <Button variant="outlined" onClick={handlePostcodeSearch} sx={{ marginLeft: 2, minWidth: 108 }}>
             
          </Button>
        </Box>
        <TextField
          label="  ."
          fullWidth
          required
          value={detailAddress}
          onChange={(e) => setDetailAddress(e.target.value)}
          margin="normal"
        />

        {/* 12.   */}
        <EquipmentChecklist
          selectedEquipment={selectedEquipment}
          setSelectedEquipment={setSelectedEquipment}
        />

        <Box sx={{ marginTop: 2 }}></Box>

        {/* 13. ,   */}
        <Box sx={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
          <Button variant="outlined" onClick={() => navigate(-1)} fullWidth sx={{ marginRight: 2 }}>
            
          </Button>
          <Button variant="contained" type="submit" fullWidth>
            
          </Button>
        </Box>
      </form>
    </Box>
  );
};

export default SpaceRegistration;
